body{font-family:'Playfair Display',sans-serif;font-size:25px;color:#010000;font-weight:400;font-style:normal}a{-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}a:hover{color:#ff1755;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out}h1{text-align:center}h2{text-align:center}header{position:relative;display:block;width:100%;background:#010000;color:#fff;height:90px}header .logo{position:absolute;font-size:45px;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header ul li{list-style-type:none;display:inline-block;margin-right:10px}header ul li a{font-size:25px;line-height:normal;color:#fff}header ul li a:hover{text-decoration:none}